@import './global.scss';

.menu {
  background-color: $primary-color;
  box-shadow: 0px -2px 10px gray;
  width: 100%;
  height: 10vh;
  position: fixed;
  z-index: 2;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.menu ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  padding: 0px;
}

.homeIcon {
  background-image: url('../../images/user.png');
  background-size: 30px;
  background-repeat: no-repeat;
  background-position: center;
  width: 20vw;
  height: 10vh;
  cursor: pointer;
  &:active {
    background-image: url('../../images/user-active.png');
  }
  &:hover {
    background-image: url('../../images/user-active.png');
  }
  &:focus {
    outline: 0;
  }
}

